<template>
    <div class="container">
        <!-- 登录页面 -->
        <div v-if="visible" class="login_page">
            <div class="pan">
                <img class="profile" src="../assets/logo.png" />
                <input
                        class="user_name_input"
                        type="text" maxlength="14"
                        placeholder="输入昵称/回车确认"
                        v-model="username" @keyup.enter="onConfirm">
            </div>
            <img class="bg" src="../assets/bg.jpeg" alt="">
        </div>
        <!-- 聊天页面 -->
        <div class="chat_area" :style="styleObject">
            <div class="bar">
                <span class="count">人数：{{count}}</span>
                <span class="exit_btn" @click="onExitRoom">退出房间</span>
            </div>
            <ul ref="msg_box">
                <li v-for="item in items" v-bind:key="item.index">
                    <p v-if="item.type === 'message'" :class="item.type">{{item.who}}: {{ item.content }}</p>
                    <p v-else-if="item.type === 'leave'" :class="item.type">{{item.who}} 离开了房间 {{item.time}}</p>
                    <p v-else :class="item.type">欢迎 {{item.who}} 进入房间 {{item.time}}</p>
                </li>
            </ul>
            <input
                    class="input_message"
                    type="text"
                    placeholder="Type here... & press enter send"
                    @keyup.enter="onSend" v-model="message">
        </div>
    </div>
</template>

<script>
  import io from 'socket.io-client'
  import moment from "moment"; //引入socket.io-client

  export default {
    name: "ChatArea",
    data() {
      return {
        visible: true,
        styleObject: { height: '400px' },
        message: '',
        username: '',
        count: 0,
        items: [
          // {who: '张三', content: '老铁们，小红心点起来', type: 'message'},
        ],
      }
    },
    mounted() {
      this.socket = io(process.env.VUE_APP_URL || "/")

      // 1. 监听服务端的login事件, 获取房间人数
      this.socket.on('user joined', ({ username, numUsers, time }) => {
        this.count = numUsers;
        this.items.push({who: username.user, content: '', type: 'broadcast', time: moment(time).format('HH:mm:ss')})
      })

      // 2. 监听当前客户端的login事件，因为上面的广播事件不会退给自己
      this.socket.on('login', ({numUsers}) => {
        this.count = numUsers;
      })

      // 3. 监听服务器推送的其他用户发送的消息
      this.socket.on('new message', ({username, message}) => {
        this.items.push({who: username.user, content: message, type: 'message'})
      })

      // 监听服务器广播的用户离开事件
      this.socket.on('user left', ({username, numUsers, time}) => {
        this.count = numUsers;
        this.items.push({who: username.user, content: '', type: 'leave', time: moment(time).format('HH:mm:ss')})
      })

      // 获取浏览器屏幕高度自动调整聊天室高度
      this.styleObject.height = document.documentElement.clientHeight-20+'px'
      window.onresize = () => {
        this.styleObject.height = document.documentElement.clientHeight-20+'px'
      };
    },
    methods: {
      // 1. 登录界面登录后向服务器发送添加用户事件
      onConfirm() {
        if (this.username.trim() === '') {
          this.$message.error('昵称不能为空哦，骚年~')
          return
        }
        this.visible = false
        this.$message.success('哟，进入房间了，还不快点聊起呀~')
        this.socket.emit('add-user', {user: this.username})
      },
      // 2. 向服务器发送消息
      onSend() {
        this.$emit('on-send', this.message)
        if (this.message.trim() === '') {
          this.$message.warning('不能发空哦，骚年~')
          return
        }
        // 更新自己房间聊天信息
        this.items.push({who: '我', content: this.message.trim(), type: 'message'})
        // 向服务器发送信息
        this.socket.emit('new message', this.message.trim())
        this.message = ''
      },
      // 3. 向服务器发送离开房间消息
      onExitRoom() {
        // 更新自己房间状态
        this.items.push({who: '我', content: '', type: 'leave', time: moment().format('HH:mm:ss')})
        // 向服务器发送信息
        this.socket.emit('disconnect')
      },
      handleScrollBottom() {
        const ul = this.$refs.msg_box
        ul.scrollTop = ul.scrollHeight
      },
    },
    updated() {
      this.handleScrollBottom()
    }
  }
</script>

