import Vue from 'vue';
import VueRouter from 'vue-router';
import Category from "../views/Category";
import ChatArea from "../views/ChatArea";
import Chart from "../views/Chart";
import DragDrop from "../views/DragDrop";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Category',
    component: Category
  },
  {
    path: '/chat',
    name: 'Chat',
    component: ChatArea
  },
  {
    path: '/chart',
    name: 'Chart',
    component: Chart
  },
  {
    path: '/drag-drop',
    name: 'DragDrop',
    component: DragDrop
  }
]

const router = new VueRouter({routes})

export default router
