<template>
    <div class="container">
        <el-row :gutter="20">
            <el-col :span="12">
                <div class="drag_eara">
                    <div class="el-header">可选组件</div>
                    <drag class="drag_item" :transferData="{ type: 'input' }">
                        <span class="handle"></span>
                        <el-input
                                placeholder="输入框"
                                disabled
                                class="form_item_label">
                        </el-input>
                        <el-input
                                placeholder="请输入内容"
                                clearable
                                class="form_component">
                        </el-input>
                    </drag>
                    <drag class="drag_item" :transferData="{ type: 'select' }">
                        <span class="handle"></span>
                        <el-input
                                placeholder="下拉框"
                                disabled
                                class="form_item_label">
                        </el-input>
                        <el-select v-model="value" placeholder="请选择" class="form_component">
                            <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </drag>
                    <drag class="drag_item" :transferData="{ type: 'datetime' }">
                        <span class="handle"></span>
                        <el-input
                                placeholder="输入框"
                                disabled
                                class="form_item_label">
                        </el-input>
                        <el-date-picker
                                type="date"
                                placeholder="选择日期" class="form_component">
                        </el-date-picker>
                    </drag>
