                    <el-button type="primary">保存</el-button>
                </div>
                <drop class="drop_eara" :class="{ over }"
                      @dragover="over = true"
                      @dragleave="over = false"
                      @drop="handleDrop">
                    <SlickList lockAxis="y" v-model="items" tag="ul"
                               @sort-end="onSortEnd"
                               @input="onResult"
                    >
                        <SlickItem v-for="(item, index) in items" :index="index" :key="index" tag="li">
                            <div v-if="item.type === 'input'" class="form_item">
                                <span class="handle"></span>
                                <el-input
                                        placeholder="编辑"
                                        clearable
                                        v-model="items[index].label"
                                        class="form_item_label">
                                </el-input>:
                                <el-input
                                        placeholder="请输入内容"
                                        clearable
                                        class="form_component">
                                </el-input>
                            </div>
                            <div v-else-if="item.type === 'select'" class="form_item">
                                <span class="handle"></span>
                                <el-input
                                        placeholder="编辑"
                                        clearable
                                        v-model="items[index].label"
                                        class="form_item_label">
                                </el-input>:
                                <el-select
                                        placeholder="请选择"
                                        v-model="value"
                                        class="form_component">
                                    <el-option
                                            v-for="item in options"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                            <div v-else-if="item.type === 'datetime'" class="form_item">
                                <span class="handle"></span>
                                <el-input
                                        placeholder="编辑"
                                        clearable
                                        v-model="items[index].label"
                                        class="form_item_label">
                                </el-input>:
                                <el-date-picker
                                        type="datetime"
                                        placeholder="选择日期时间"
                                        class="form_component">
                                </el-date-picker>
                            </div>
                        </SlickItem>
                    </SlickList>
                </drop>
            </el-col>
        </el-row>
    </div>
</template>

<script>
  import { Drag, Drop } from 'vue-drag-drop';
  import { SlickList, SlickItem } from 'vue-slicksort';

  export default {
    name: "DragDrop",
    components: {
      Drag,
      Drop,
      SlickItem,
      SlickList,
    },
    data: function () {
      return {
        over: false,
        items: [],
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        value:'',
        form_name: ''
      };
    },
    methods: {
      handleDrop(data) {
        this.over = false;
        this.items.push({type: data.type, label: ''})
      },
      onSortEnd({ event, newIndex, oldIndex, collection }) {
        console.log(event, newIndex, oldIndex)
        console.log('collection', collection)
      },
      onResult(arr) {
        console.log('arr', arr)
      }
    },
  }
</script>

